
import { defineComponent } from "vue";
import Timer from "./Timer.vue";

export default defineComponent({
  name: "Formulario",
  emits: ["whenAddTask"],
  components: {
    Timer,
  },
  data() {
    return {
      description: "",
    };
  },
  methods: {
    endTask(elapsedTime: number): void {
      this.$emit("whenAddTask", {
        durationinSeconds: elapsedTime,
        description: this.description,
      });
      this.description = "";
    },
  },
});
