
import { defineComponent } from "vue";

export default defineComponent({
  name: "Botao",
  emits: ["clicked"],
  props: {
    isDisabled: {
      type: Boolean,
    },
    icon: {
      type: String,
      required: true,
    },
    text: {
      type: String,
      required: true,
    },
  },
  methods: {
    clicked(): void {
      this.$emit("clicked");
    },
  },
});
